.footer-section{
    background: #161619;
    width: 370px;
    margin: 0 auto;
    padding: 10px;
    color: #F5F5F5;
    border-radius: 0px 0px 20px 20px;
}
svg{
    width: 40px;
    padding: 0px 10px;
    fill: #918E9B;
}
.footer-section .twitter svg:hover{
    fill: #00acee !important;
}
.footer-section .facebook svg:hover{
    fill: #4267B2;
}
.footer-section .github svg:hover{
    fill: #7a50b9;
}
.footer-section .instagram svg:hover{
    fill: #bc2a8d;
}