.info-section{
    width: 370px;
    background: #1A1B21;
    margin: 0 auto;
    padding: 10px;
    color: #F5F5F5;
    border-radius: 20px 20px 0px 0px;
}
.info-text{
    margin: 0 auto;
    width: 247px;
    font-size: 14px;
    line-height: 150%;
}
.user-name{
    font-family: 'Inter';
    font-size: 25px;
    line-height: 30px;
    color: #FFFFFF;
    margin-bottom: 0px;
}
.medium-text{
    font-family: 'Inter';
    font-size: 12.8px;
    line-height: 15px;
    text-align: center;
    margin-top: 0px;
    color: #F3BF99;
}
.profile-img{
    width: 230px;
}
.link-btn{
    display: flex;
    justify-content: space-between;
}
.btn{
    padding: 0em 1.5em 0em 0.5em;
}
.email-btn{
    background: #FFFFFF;
    border: 1px solid #D1D5DB;
    box-sizing: border-box;
    border-radius: 6px;
    color: #374151;
    display: flex;
    justify-content: space-around;
    cursor: pointer;
}
.email-btn a{
    color: #374151;
}
.btn svg{
    width: 30px;
    padding-top: 0.6em;
}

.github-btn{
    background-color: #7a50b9;
    border: 1px solid #D1D5DB;
    box-sizing: border-box;
    border-radius: 6px;
    display: flex;
    justify-content: space-around;
}
a{
    text-decoration: none;
    color: #FFFFFF;
}